import { NextApiRequest, NextApiResponse } from "next";
import * as AuthService from "../service/AuthService";

export const loginUser = async (req: NextApiRequest, res: NextApiResponse) => {
    const { email, password, token } = req.body;

    try {
        let generatedToken;

        if (token) {
            generatedToken = await AuthService.loginWithToken(token);
        } else if (email && password) {
            generatedToken = await AuthService.login(email, password);
        } else {
            return res.status(400).json({ error: "Por favor proporciona el email y la contraseña o un token" });
        }

        return res.status(200).json({ token: generatedToken });
    } catch (error: any) {
        return res.status(401).json({ error: error.message });
    }
};
